import React, {useContext, useEffect} from 'react'
import Porfolio from '../../../common/porfolio/porfolio'
import PeopleContext from '../../../../context/people/people-context'

const TeamSection = () => {
  const peopleContext = useContext(PeopleContext)

  useEffect(() => {
    peopleContext.getPeople()
  }, [])

  const findPerson = (items, id) => {
    return items.find(item => item.id === id)
  }

  const {people} = peopleContext

  const byron = findPerson(people, 1)
  const lilly = findPerson(people, 2)
  const robert = findPerson(people, 3)
  const carmen = findPerson(people, 4)

  return (
    <section className="motivation pb-7 pb-md-8">
      <div className="rellax fly-shape fly-shape_center" data-rellax-speed="-2.5">
        <img
          src="data:image/svg+xml;utf8;base64,"/>
      </div>
      <div className="rellax fly-shape fly-shape_center" data-rellax-speed="-2">
        <img
          src="data:image/svg+xml;utf8;base64,"/>
      </div>
      <div className="container">
        <h2 className="text-md-center">Team of professionals</h2>

        <div className="row justify-content-between text-center">
          <div className="col-md-4 pt-md-6">
            {byron && <Porfolio person={byron}/>}
          </div>
          <div className="col-md-4">
              {robert && <Porfolio person={robert}/>}
              {lilly && <Porfolio person={lilly}/>}
          </div>
          <div className="col-md-4 pt-md-7">
                {carmen && <Porfolio person={carmen}/>}
          </div>
        </div>
      </div>
    </section>
  )
}

export default TeamSection
