import React from 'react'

const HeaderShapes = () => {
  return (
    <div className="shapes">
      <img className="rellax fly-shape" data-rellax-speed="2"
           src="data:image/svg+xml;utf8;base64,"/>
        <circle fill="red" cx="10" cy="10" r="10"/>
      <div className="rellax fly-shape fly-shape_center" data-rellax-speed="-5">
        <img
          src="data:image/svg+xml;utf8;base64,"/>
          <circle fill="red" cx="10" cy="10" r="10"/>
      </div>
      <div className="rellax fly-shape fly-shape_right" data-rellax-speed="-2">
        <img
          src="data:image/svg+xml;utf8;base64,"/>
          <circle fill="red" cx="10" cy="10" r="10"/>
      </div>
    </div>
  )
}

export default HeaderShapes
