import React, {useState, useEffect} from 'react';
import { Link, NavLink } from 'react-router-dom'
import MobileNav from './mobile-nav/mobile-nav'
import './navbar.scss'
import { useTranslation } from 'react-i18next';
import ReactCountryFlag from "react-country-flag";

function Navbar({light}) {
  const [active, setActive] = useState('init');
  const [navbar, setNavbar] = useState(null)

  useEffect(() => {
    setNavbar(document.querySelector('.navbar'))
  }, [])

  if (navbar) {
    window.onscroll = function () {
      if (window.scrollY > 100) {
        navbar.classList.add('short');
      } else {
        navbar.classList.remove('short');
      }
    }
  }
  const { t, i18n } = useTranslation();
  return (
    <React.Fragment>
      <nav className={`navbar navbar-expand-lg fixed-top ${!light ? 'bg-primary' : 'navbar-light bg-white'}`}>
        <div className="container">

          <Link className="navbar-brand" to="/"><img src="/assets/img/common/logo.png" alt="" className="img-fluid logo"/></Link>
          <button className="navbar-toggler" type="button" onClick={() => setActive('active')}>
            <span className="navbar-toggler-icon" />
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <NavLink className="nav-link" to="/projects" activeClassName="active">{t('navbar.projects')}</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/porfolio" activeClassName="active">{t('navbar.porfolio')}</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/blog" activeClassName="active">Blog</NavLink>
              </li>
            </ul>

            <Link className={`btn btn-primary py-2 ml-5 btn-warning`}
                  to="/contact">Contact</Link>
            <div className={`d-flex ml-3`}>
                <div onClick={() => i18n.changeLanguage('fr')}><ReactCountryFlag styleProps={{
                  cursor: 'pointer'

                }} code="fr" svg /></div>

                <div onClick={() => i18n.changeLanguage('en')}><ReactCountryFlag  styleProps={{
                  'margin-left': '15px',
                  cursor: 'pointer'

                }} code="us" svg /></div>
                <div onClick={() => i18n.changeLanguage('en')}><ReactCountryFlag styleProps={{
                  'margin-left': '15px',
                  cursor: 'pointer'
                }} code="ru" svg /></div>

            </div>
          </div>

        </div>
      </nav>
      <MobileNav active={active} setActive={(value) => setActive(value)} />
    </React.Fragment>
  );
}

export default Navbar;
