import React from 'react'
import {Link} from 'react-router-dom'
import { useTranslation } from 'react-i18next';

const Who = (props) => {
    const { t } = useTranslation();
    return (
    <Link to="/porfolio" className="who">
      <div className="container">
        <h2>{t('home.about')}</h2>
      </div>
    </Link>
  )
}

export default Who
